export function inject_css(css) {
  const CSS_ID = 'codeflask-style';
  
  if (!css) {
    return false;
  }

  if (document.getElementById(CSS_ID)) {
    return true;
  }

  const style = document.createElement('style');

  style.innerText = css;
  style.id = CSS_ID;
  document.head.appendChild(style);

  return true;
}
